.card-container{
    margin-top: 30px;
    width: 30%;
    height: 520px;
    border: 1px solid black;
    border-radius: 30px;
    margin: 22px;
    position: relative;
    display: inline-block;
}
.card-container:hover{
    -webkit-transform: scaleX(1.1);
            transform: scaleX(1.1);
    -webkit-transform: scaleY(1.1);
            transform: scaleY(1.1);
    transition: 400ms;
    cursor: pointer;
}

