.card-container{
    margin-top: 30px;
    width: 30%;
    height: 520px;
    border: 1px solid black;
    border-radius: 30px;
    margin: 22px;
    position: relative;
    display: inline-block;
}
.card-container:hover{
    transform: scaleX(1.1);
    transform: scaleY(1.1);
    transition: 400ms;
    cursor: pointer;
}

.card-container h1{
    text-align: center;
    padding-top: 20px;
    padding-bottom: 10px;
    border-bottom: 1px solid black;
}

.card-container h1:hover{
    text-decoration: underline;
}

.card-img_container{
    width: 100%;
    height: 300px;
}

.card-img_container img {
    width: 300px;
    height: 300px;
    margin-left: 70px;
}

.body-card{
    width: 100%;
    height: auto;
    display: flex;
    border-top: 1px solid black;
}

.body-card_specs{
    width: 50%;
    height: auto;
    padding: 30px;
}

.container-button{
    width: 50%;
    height: auto;
}

.container-button button{
    font-weight: 700;
    font-size: 20px;
    border-radius: 30px;
    border: 1px solid black;
    background: transparent;
    margin-top: 50px;
    width: 90%;
    height: 50px;
    outline:none;
}

.container-button button:hover{
  transition: 300ms;
  background-color:#128c7e;
  border: 1px solid #075e54;
  cursor: pointer;
}

.body-card_specs h2 {
    font-size: 15px;
    padding: 3px;
}